import React, { Component } from 'react'
import Card from './card'
import Header from './header'
import Foot from './footer'

class Webpage extends Component { 
    constructor(props) {
        super(props)
    
        this.state = {
             region:'ts',
             
        }
    }

    handler = (e)=>{
        this.setState({
            region:e.target.name
        })
    }
    

    render() {
        const Width = window.innerWidth; 
        return (
        <>
        { this.state.region ==='ts'?
            <>
            <Header handle={this.handler} region='TELANGANA'/>
            <div className={Width>950 ? "CardH" : "CardV"}> 
                <Card heading="Active" num={this.props.activets} color="#66b3ff" /> 
                <Card heading="New Cases" num={this.props.newts} color="orange" /> 
                <Card heading="Deaths" num={this.props.deathsts} color="#ff4d4d" /> 
            </div>
            <Foot />
            </> :
             <>
             <Header handle={this.handler} region='INDIA'/>
             <div className={Width>950 ? "CardH" : "CardV"}> 
                 <Card heading="Active" num={this.props.activeind} color="#66b3ff" /> 
                 <Card heading="New Cases" num={this.props.newind} color="orange" /> 
                 <Card heading="Deaths" num={this.props.deathsind} color="#ff4d4d" /> 
             </div>
             <Foot />
             </>
        }
        </>
        )
    }
}

export default Webpage
