import React, { Component } from 'react'

class card extends Component{
    
    heading = {
        backgroundColor:this.props.color,
        height:'70px',
        lineHeight:'70px',
        color:'white',
        WebkitTextStroke:'.8px black'
    }
    
   para = {
        lineHeight:'140px',
        height:'160px',
        backgroundColor:'white',
        color:'grey',
        WebkitTextStroke:'.8px black'
    }
    render(){ 
    return (
        <div className="Card">
             <h2 style={this.heading} className="heading">{this.props.heading}</h2> 
             <hr />
             <p style={this.para} className="num">{this.props.num}</p>
        </div>
    )
}
}

export default card
