import React,{Component} from 'react'

class header extends Component  { 
    
    date = new Date()
    
    headbar = {
        fontSize:'26px',
        padding:'50px',
        backgroundColor:'#3385ff',
        color:'white',
        WebkitTextStroke:'0.01px black',
    }


    render(){
        return (
            <div className="headbar" style={this.headbar}> 
                <h1 className="Sitename">Corona - Instant Updates </h1> 
                <br />
                <hr /> <br /><br />
                <p className="caption">Stay Aware - Stay Safe </p> 
                <br/>
                <button onClick={this.props.handle} name="ind">India</button>
                <button onClick={this.props.handle} name="ts">Telangana</button>
                <br/>
                <p style={{fontSize:'40px', fontFamily:'Lexend'}}>Corona Cases in  <span style={{fontSize:'45px'}}>{this.props.region}</span> </p>
                <p style={{color:'#f2f2f2',fontSize:'13px'}}>(as of Latest Updated i.e. {this.date.getDate()} . {this.date.getMonth()+1} . {this.date.getFullYear()} )</p>
                
            </div>
        )
    }
}

export default header
