import React from 'react'

function Loading() {
    return (
        <div style={{height:'100vh',
                    paddingTop:'30vh',
                    textAlign:'center',
                    boxSizing:'border-box',
                    fontSize:'2rem',
                    fontWeight:'bolder',
                    fontFamily:'Lexend',
                    lineHeight:'55px'
                    }}>
            Loading... <br/>
            Please Wait ...!!
        </div>
    )
}

export default Loading
