import React from 'react'

function footer() {
    const foot = {
        backgroundColor:'#66a3ff',
        padding:'20px',
        color:'white',
        fontFamily:'Lexend',
        fontSize:'15px',
        letterSpacing:'1px',
        lineHeight:'35px'
    }
    
    return (
        <div className="foot" style={foot}>
            <p>A product of <span style={{fontWeight:'bold',fontSize:'17px'}}>_TeamTsm_</span></p> 
            <p>An attempt to keep our loved ones Aware and Safe. </p>
            <p>&copy; All rights reserved 2020-21</p>
            
        </div>
    )
}

export default footer
