import './App.css';
import React, { Component } from 'react'
import WebPage from './components/Webpage'
import Loading from './components/Loading'

class App extends Component { 
    constructor(props) {
        super(props)
        this.state = { 
             newCasests:0,
             newCasesind:0,
             TotActivets:0,
             TotActiveind:0,
             Deathsts:0,
             Deathsind:0,
             isLoading:false
        }
    }
    
    componentDidMount(){
        this.setState({isLoading:true})
        fetch("https://api.apify.com/v2/key-value-stores/toDWvRj1JpTXiM8FF/records/LATEST?disableRedirect=true")
            .then(resp => {
                return resp.json()
            }) 
             .then(data => { 
                 this.setState(prev=>{ 
                     return{
                        ...prev,
                        newCasests:data['regionData'][31]['newInfected'] ,
                        newCasesind:data['activeCasesNew'],
                        TotActivets:data['regionData'][31]['totalInfected'] ,
                        TotActiveind:data['activeCases'],
                        Deathsts:data['regionData'][31]['deceased'] ,
                        Deathsind:data['deaths'],
                        isLoading : false
                     }
                 })
             })
            .catch(() => {
                console.log("errrrror")
            })
    }

    render() {
        
        return (
            <div className="App">
              {this.state.isLoading ? <Loading /> : <WebPage activets={this.state.TotActivets} activeind={this.state.TotActiveind} newts={this.state.newCasests} newind={this.state.newCasesind} deathsts={this.state.Deathsts} deathsind={this.state.Deathsind} /> }
            </div>
        )
    }
}

export default App
